import {authAPI, securityAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET__USERS__DATA = 'SET__USERS__DATA-auth-socialNetwork';
const SET__CAPTCHA__URL = 'SET__CAPTCHA__URL-auth-socialNetwork';


let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    isCaptcha: null
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET__USERS__DATA: {
            return {
                ...state,
                ...action.data,
            }
        }

        case SET__CAPTCHA__URL: {
            return {
                ...state,
                isCaptcha: action.captchaUrl
            }
        }


        default: {
            return state;
        }
    }
}


export const setUsersData = (id, email, login, isAuth) => ({type: SET__USERS__DATA, data: {id, email, login, isAuth}});
export const setCaptchaUrl = (captchaUrl) => ({type: SET__CAPTCHA__URL, captchaUrl});


export const authData = () => {
    return async (dispatch) => {
        let data = await authAPI.getAuthData()
        const {id, login, email} = data.data;
        if (data.resultCode === 0) {
            dispatch(setUsersData(id, email, login, true));
        }
        if (data.resultCode === 10) {
            getCaptcha();
        }
    }
}

export const login = (email, password, rememberMe, captcha) => {
    return async (dispatch) => {
        let data = await authAPI.login(email, password, rememberMe, captcha)
        if (data.resultCode === 0) {
            dispatch(authData());
        } else {
            if (data.resultCode === 10) {
                dispatch(getCaptcha());
                const message = data.messages.length > 0 ? data.messages[0] : 'some Error';
                dispatch(stopSubmit('login', {_error: message}));
            } else {
                const message = data.messages.length > 0 ? data.messages[0] : 'some Error';
                dispatch(stopSubmit('login', {_error: message}));
            }
        }

    }
}

export const logout = () => {
    return async (dispatch) => {
        let data = await authAPI.logout()
        if (data.resultCode === 0) {
            dispatch(setUsersData(null, null, null, false));
        }
    }
}

export const getCaptcha = () => {
    return async (dispatch) => {
        let data = await securityAPI.getCaptchaUrl();
        dispatch(setCaptchaUrl(data));
    }
}


export default authReducer;