import React, {useEffect, useState} from 'react';
import style from './Profile.module.css';
import profileBg from '../../assets/img/header_images.jpg';
import {useDispatch, useSelector} from "react-redux";
import {getUserProfile, getUserStatus, savePhoto, sendData} from "../../redux/profileReducer";
import Preloader from "../../common/Preloader/Preloader";
import {withRouter} from "react-router-dom";
import userImg from '../../assets/img/userAvatar.png';
import {Redirect} from 'react-router-dom';
import cn from 'classnames';
import {compose} from 'redux';
import ProfileStatus from "./ProfileStatus";
import ProfileInfoFormWithRedux from "./ProfileInfoForm";

const ProfileInfo = (props) => {
    const {profile, status} = useSelector(state => state.profile);
    const {isAuth} = useSelector(state => state.auth);
    const authId = useSelector(state => state.auth.id);
    const [editMode, setEditMode] = useState(false);

    const addEditMode = () => {
        setEditMode(true);
    }

    let userId = props.match.params.userId;
    if (!userId) {
        userId = authId;
        if (!userId) {
            props.history.push('/login');
        }
    }

    const dispatch = useDispatch();

    const getPhoto = (e) => {
        dispatch(savePhoto(e.target.files[0]));
    }

    const onSubmit = (formData) => {
        dispatch(sendData(formData)).then(
            () => {
                setEditMode(false);
            }
        );
    }

    useEffect(() => {
        dispatch(getUserProfile(userId));
        dispatch(getUserStatus(userId));
    }, [status, userId]);

    if (!isAuth) return <Redirect to={'/login'}/>

    {
        if (!profile) {
            return <Preloader/>
        } else {

            return (
                <>
                    <div className={style.background}>
                        <img className={style.image} src={profileBg} alt="Phone"/>
                    </div>

                    <div className={style.description}>
                        <div className={cn(style.avatar, style.infoAvatar)}>
                            <img className={style.avatarImg}
                                 src={!profile.photos.large ? userImg : profile.photos.large} alt="Avatar"/>
                            {!props.match.params.userId &&
                            <label htmlFor="file" className={cn(style.fileButton, 'button')}>select photo<input
                                className={style.selectFile} id='file' type="file" onChange={getPhoto}/></label>}
                        </div>
                        {!editMode && !props.match.params.userId &&
                        <button className={cn('button', style.editBtn)} onClick={addEditMode}>edit</button>}
                        <ProfileStatus status={status}/>
                        {editMode ? <ProfileInfoFormWithRedux initialValues={profile} onSubmit={onSubmit}/> :
                            <ProfileBlock profile={profile}/>}
                    </div>
                </>
            )
        }
    }
}

const ProfileBlock = ({profile}) => {
    return (
        <div className={style.profileWrapper}>
            <div><b>aboutMe: </b>{profile.aboutMe}</div>
            <div><b>fullName: </b>{profile.fullName}</div>
            <div><b>lookingForAJob: </b>{profile.lookingForAJob ? 'yes' : 'no'}</div>
            <div><b>lookingForAJobDescription: </b>{profile.lookingForAJobDescription}</div>
            {Object.keys(profile.contacts).map(key => <Contacts key={key} title={key} value={profile.contacts[key]}/>)}
        </div>
    )
}


const Contacts = ({title, value}) => {
    return (
        <div><b>{title}:</b> {value}</div>
    )
}


export default compose(
    withRouter
)(ProfileInfo);
