import React from 'react';
import style from './Navbar.module.css'
import {NavLink} from 'react-router-dom'
import cn from 'classnames';

const Navbar = () => {
    const [active, setActive] = React.useState(false);
    const [showNavbar, setShowNavbar] = React.useState(false);

    const toggleActive = () => {
        setActive(!active);
        setShowNavbar(!showNavbar);
    }

    return (
        <>
            <div className={cn({[style.active]: active}, style.hamburger)} onClick={toggleActive}>
                 <span></span>
             </div>
            <div className={cn({[style.active]: active}, style.navbar)}>

                <nav className={style.nav}>
                    <ul className={style.navMenu}>
                        <li className={style.item} ><NavLink className={style.navLink} activeClassName={style.active}
                                                                           to="/profile">Profile</NavLink></li>
                        <li className={style.item}><NavLink className={style.navLink} activeClassName={style.active} to="/dialogs">Messages</NavLink></li>
                        <li className={style.item}><NavLink className={style.navLink} activeClassName={style.active} to="/users">Users</NavLink></li>
                        <li className={style.item}><NavLink className={style.navLink} activeClassName={style.active} to="/login">Login</NavLink></li>
                        <li className={style.item}><NavLink className={style.navLink} activeClassName={style.active} to="/news">News</NavLink></li>
                        <li className={style.item}><NavLink className={style.navLink} activeClassName={style.active} to="/music">Music</NavLink></li>
                    </ul>
                </nav>
            </div>
        </>
    )
}

export default Navbar;