import React from 'react';
import {reduxForm} from "redux-form";
import style from './Login.module.css';
import {requiredField} from "../../common/utilits/validators";
import {createField, Input} from "../../common/FormControls/FormControls";
import {useDispatch, useSelector} from "react-redux";
import {login} from "../../redux/authReducer";
import {Redirect} from 'react-router-dom';

const LoginForm = (props) => {
    const {isCaptcha} = useSelector(state => state.auth);
    console.log(isCaptcha);
    return (
        <>
            <form onSubmit={props.handleSubmit} className={style.loginForm}>
                <div className='formInput'>{createField('text', 'Login', Input, 'email', requiredField)}</div>
                <div className='formInput'>{createField('password', 'Password', Input, 'password', requiredField)}</div>
                <div>{createField('checkbox', null, 'input', 'rememberMe', null, {className: 'formCheckbox'})}</div>
                <div><button className={'button' + ' ' + style.loginButton}>Login</button></div>
                {isCaptcha && <img src={isCaptcha.url} alt=""/>}
                {isCaptcha &&  <div className='formInput'>{createField('text', 'antibotSimbols', Input, 'captcha', requiredField)}</div>}
                {props.error && <div className={style.loginError}>
                    {props.error}
                </div>}

            </form>
        </>
    )
}

const ReduxWrapperForm = reduxForm({
    form: 'login'
})(LoginForm);

const Login = (props) => {
    const {isAuth} = useSelector(state => state.auth);
    const dispatch = useDispatch();

    const onSubmit = (formData) => {
        dispatch(login(formData.email, formData.password, formData.rememberMe, formData.captcha));
        console.log(formData);
    }


    if(isAuth) {
        return <Redirect to={'/profile'}/>
    }
    return (
        <div>
            <h2>Login</h2>
            <ReduxWrapperForm onSubmit={onSubmit}/>
        </div>
    )
}

export default Login;