const ADD__MESSAGE = 'ADD__MESSAGE-dialogs-socialNetwork';

let initialState = {
    messages: ['hello', 'How, are you!', 'Hi', 'My name is Kesha!', 'I`m a cat'],
};

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD__MESSAGE: {
            return {
                ...state,
                messages: [...state.messages, action.messageBody],
            }
        }

        default: {
            return state;
        }
    }

}

export const addMessage = (messageBody) => ({type: ADD__MESSAGE, messageBody});

export default dialogsReducer;