import React, {useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import style from "./Paginator.module.css";
import rightArrow from "../../assets/img/arrowRight.svg"
import leftArrow from "../../assets/img/arrowLeft.svg"

import {
    getUsers,
    setCurrentPage,
}
    from "../../redux/usersReducer";
import cn from "classnames";


const Paginator = (props) => {
    const {portionSize, totalUsersCount, currentPage} = useSelector(state => state.users);
    const dispatch = useDispatch();

    let pages = [];
    let pageCount = Math.ceil(totalUsersCount / portionSize);

    for (let i = 1; i <= pageCount; i++) {
        pages.push(i);
    }

    let portionCount = 10;

    const totalPortionCount = pageCount / portionCount;
    const [portionNumber, setPortionNumber] = useState(Math.ceil(currentPage/10));
    const leftBorderPortion = (portionNumber - 1) * portionCount + 1;
    const rightBorderPortion = (portionNumber * portionCount);
    console.log(portionNumber);


    const addUsers = (page) => {
        dispatch(getUsers(portionSize, page));
        dispatch(setCurrentPage(page));
    }

    return (
        <div className={style.pagesRow}>
            {portionNumber > 1 && <button className={style.pageButton} onClick={() => setPortionNumber(portionNumber -1)}>
                <img src={leftArrow} alt=""/></button>}
            <div className={style.pages}>
                {pages.filter(page => page >= leftBorderPortion && page <= rightBorderPortion).map((p, index) => <span
                    onClick={() => addUsers(p)} key={p + '_' + index}
                    className={cn({[style.active]: p === currentPage}, style.page)}>{p}</span>)}
            </div>
            {totalPortionCount > portionNumber && <button className={style.pageButton} onClick={() => setPortionNumber(portionNumber +1)}><img src={rightArrow} alt=""/></button>}

        </div>
    )
}

export default Paginator;