import React from 'react';
import style from './Dialogs.module.css';
import {useDispatch, useSelector} from "react-redux";
import {addMessage} from "../../redux/dialogsReducer";
import cn from 'classnames';
import  {Field,reduxForm} from "redux-form";
import {maxLength, requiredField} from "../../common/utilits/validators";
import {createField, Textarea} from "../../common/FormControls/FormControls";
import DialogsItem from "./DialogsItem/DialogsItem";
import Message from "./Message/Message";
import {Redirect} from "react-router-dom";

const maxLength50 = maxLength(50);

const FormMessage = (props) => {
    let {message} = useSelector(state => state.dialogs);

    return (
        <form onSubmit={props.handleSubmit}>
            <div className={style.addMessage}>
                {createField(null, 'your message', Textarea, 'textarea', [requiredField, maxLength50], {value: message, className: style.textarea})}
                <div className={style.buttonWrap}>
                    <button className={cn('button', style.button)}>send</button>
                </div>
            </div>
        </form>
    )
}

const FormMessageWithReduxForm = reduxForm({
    form: 'message'
})(FormMessage);

const Dialogs = (props) => {
    let {messages} = useSelector(state => state.dialogs);
    let {isAuth} = useSelector(state => state.auth);
    const dispatch = useDispatch();


    if (!isAuth) return <Redirect to={'/login'}/>
    const submit =(formData) => {
        dispatch(addMessage(formData.textarea));
    }

    return (
        <div className={cn('content', style.dialogsContent)}>
            <div className={style.names}>
                <DialogsItem name='Artem'/>
                <DialogsItem name='Ludmila'/>
                <DialogsItem name='Julia'/>
                <DialogsItem name='Kesha'/>
                <DialogsItem name='Rin'/>
            </div>
            <div className={style.messages}>
                {messages.map((el, index) => (<Message key={el + '_' + index} text={el}/>))}
                <FormMessageWithReduxForm onSubmit={submit}/>
            </div>
        </div>
    )
}


export default Dialogs;