import React  from 'react';
import style from './Profile.module.css';
import {createField, Input, Textarea} from "../../common/FormControls/FormControls";
import {reduxForm} from "redux-form";
import {useSelector} from "react-redux";

const ProfileInfoForm = ({handleSubmit, error}) => {
    const {contacts} = useSelector(state => state.profile.profile)
    return (
        <form onSubmit={handleSubmit}>
            {error && <div className={style.loginError}>
                {error}
            </div>}
            <div><b>aboutMe: </b></div>
            <div>{createField('text', 'aboutMe', Textarea, 'aboutMe', null, {className: style.infoTextarea})}</div>
            <div><b>fullName: </b></div>
            <div>{createField('text', 'fullName', Input, 'fullName', null, {className: style.infoInput})}</div>
            <div><b>lookingForAJob: </b></div>
            <div>{createField('checkbox', null, 'input', 'lookingForAJob', null, {className: style.infoInput})}</div>
            <div><b>lookingForAJobDescription: </b></div>
            <div>{createField('text', 'description', Textarea, 'lookingForAJobDescription', null, {className: style.infoTextarea})}</div>
            {Object.keys(contacts).map(key => <ContactsFormSocial key={key} title={key} value={createField('text', key, Input, 'contacts.' + key, null, {className: style.infoInput})}/>) }
            <button className='button' type={"submit"}>save</button>
        </form>
    )
}

const ContactsFormSocial = ({title, value}) => {
    return (
        <div><b>{title}:</b> {value}</div>
    )
}


const ProfileInfoFormWithRedux = reduxForm({
    form: 'infoForm'
})(ProfileInfoForm);


export default ProfileInfoFormWithRedux;