import React from 'react';
import style from './FormControls.module.css'
import cn from 'classnames';
import {Field} from "redux-form";
import {requiredField} from "../utilits/validators";

export const FormControl = ({input, meta, children, ...props}) => {
    const hasError = meta.touched && meta.error;
    return (
        <>
            <div className={cn({[style.error]: hasError}, style.formControls)}>
                {children}
            </div>
            {hasError && <p className={style.textError}>{meta.error}</p>}
        </>

    )
}

export const Textarea = (props) => {
    const {input, meta, children, ...restProps} = props;
    return (
        <FormControl {...props}><textarea {...input} {...restProps}/></FormControl>
    )
}

export const Input = (props) => {
    const {input, meta, children, ...restProps} = props;
    return (
        <FormControl className={style.formControlsInput} { ...props}><input {...input} {...restProps}/></FormControl>
    )
}

export const createField = (type, placeholder, component, name, validate, props) => {
    return <Field type={type}  placeholder={placeholder} component={component} name={name}  validate={validate} {...props}/>
}


