import {userAPI} from "../api/api";
import {objectMap} from "../common/utilits/objectMap";

const SET__CURRENT__PAGE = 'SET__CURRENT__PAGE-users-socialNetwork';
const SET__TOTAL__USERS__COUNT = 'SET__TOTAL__USERS__COUNT-users-socialNetwork';
const SET__USERS = 'SET__USERS-users-socialNetwork';
const FOLLOW = 'FOLLOW-users-socialNetwork';
const UNFOLLOW = 'UNFOLLOW-users-socialNetwork';
const SET__IS__FETCHING = 'SET__IS__FETCHING-users-socialNetwork';
const TOGGLE__FOLLOWING__PROCESS = 'TOGGLE__FOLLOWING__PROCESS-users-socialNetwork';

let initialState = {
    users: [],
    portionSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    isFollowing: []
};

const usersReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET__USERS: {
            return {
                ...state,
                users: action.users
            }
        }

        case SET__IS__FETCHING: {
            return {
                ...state,
                isFetching: action.payload
            }
        }

        case SET__CURRENT__PAGE: {
            return {
                ...state,
                currentPage: action.currentPage
            }
        }

        case SET__TOTAL__USERS__COUNT: {
            return {
                ...state,
                totalUsersCount: action.totalCount
            }
        }

        case FOLLOW: {
            return {
                ...state,
                users:
                    objectMap(state.users, action.id, 'id', {followed: true})
            }
        }

        case UNFOLLOW: {
            return {
                ...state,
                users: objectMap(state.users, action.id, 'id', {followed: false})
            }
        }

        case TOGGLE__FOLLOWING__PROCESS: {
            return {
                ...state,
                isFollowing: action.isFetching ? [...state.isFollowing, action.id] :
                    state.isFollowing.filter(id => action.id != id)
            }
        }
        default: {
            return state;
        }
    }
}

export const setCurrentPage = (currentPage) => ({type: SET__CURRENT__PAGE, currentPage});
export const setTotalUsersCount = (totalCount) => ({type: SET__TOTAL__USERS__COUNT, totalCount});
export const setUsers = (users) => ({type: SET__USERS, users});
export const follow = (id) => ({type: FOLLOW, id});
export const unfollow = (id) => ({type: UNFOLLOW, id});
export const setIsFetching = (payload) => ({type: SET__IS__FETCHING, payload});
export const toggleFollowingProcess = (isFetching, id) => ({type: TOGGLE__FOLLOWING__PROCESS, isFetching, id});


export const getUsers = (portionSize, currentPage) => {
    return async (dispatch) => {
        dispatch(setIsFetching(true));
        let data = await userAPI.getUsers(portionSize, currentPage)
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
        dispatch(setIsFetching(false));
    }
}

export const followUnfollowFlow = async (dispatch, apiData, action, userId) => {
    dispatch(toggleFollowingProcess(true, userId));
    let data = await apiData(userId);
    if (data.resultCode == 0) {
        dispatch(action(userId))
    }
    dispatch(toggleFollowingProcess(false, userId));
}


export const unFollowSuссess = (userId) => {
    return async (dispatch) => {
        let apiData = userAPI.setUnfollow.bind(userId);
        let action = unfollow;

        followUnfollowFlow(dispatch, apiData, action, userId);
    }
}

export const followSuссess = (userId) => {
    return async (dispatch) => {
        let apiData = userAPI.setFollow.bind(userId);
        let action = follow;
        followUnfollowFlow(dispatch, apiData, action, userId);
    }
}


export default usersReducer;