import React, {createRef} from 'react';
import style from './Profile.module.css';
import avatar from '../../assets/img/anonymous_steam_avatars-1-1.jpg';
import {useDispatch, useSelector} from "react-redux";
import {addPost} from "../../redux/profileReducer";
import cn from "classnames";
import  {reduxForm} from "redux-form";
import {maxLength, requiredField} from "../../common/utilits/validators";
import {createField, Textarea} from "../../common/FormControls/FormControls";
const maxLength10 = maxLength(10);

const PostForm = React.memo((props) => {
    const postValue = useSelector(state => state.profile.newPost);

    const ref = createRef();
    return (
        <form onSubmit={props.handleSubmit}>
            <div className={style.newPosts}>
                {createField(null, 'your news', Textarea, 'textarea', [requiredField, maxLength10], {value: postValue, className: style.textarea, ref: ref})}
            </div>
            <div className={style.buttonWrap}>
                <button className={cn('button', style.button)}>send</button>
            </div>
        </form>
    )
});

const PostFormWithReduxForm = reduxForm({
    form: 'posts'
})(PostForm)

const MyPosts = React.memo((props) => {
    const posts = useSelector(state => state.profile.posts);
    const dispatch = useDispatch();
    const postSubmit = (formData) => {
        dispatch(addPost(formData.textarea));
    }

    return (
        <>
            <div className={style.posts}>
                <h2>My posts</h2>
                <PostFormWithReduxForm onSubmit={postSubmit}/>
                {posts.map((obj, index) => ( <div key={index + '_'+ obj.id} className={style.post}>
                    <div className={style.postImg}>
                        <img src={avatar} alt="Аватарка"/>
                    </div>
                    <span className={style.postText}>{obj.post}</span>
                    <span className={style.likes}>Likes: {obj.likes}</span>
                </div>))}
            </div>
        </>
    )
});

export default MyPosts;