import React from 'react';
import style from './Header.module.css';
import {NavLink} from 'react-router-dom'
import {useDispatch, useSelector} from "react-redux";
import cn from 'classnames';
import {logout} from "../../redux/authReducer";


const Header = () => {
    const {id, email, login, isAuth} = useSelector(state => state.auth);
    const dispatch = useDispatch();


    const sendLogout = () => {
        dispatch(logout());
    }

    return (
        <header className={style.header}>
            <div className={style.loginWrap}>
                {isAuth && <button className={cn(style.buttonLogout, 'button')} onClick={sendLogout}>logout</button>}
                <div className={style.login}>
                    <NavLink to={'/login'}>{isAuth ? login : 'Login'}</NavLink>
                </div>
            </div>
            <h2 className={style.logo}><span>Social</span>Net</h2>
        </header>
    )
}

export default Header;