import {profileAPI, securityAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const ADD__POST = 'ADD__POST-profile-socialNetwork';
const CHANGE__VALUE = 'CHANGE__VALUE-profile-socialNetwork';
const SET__USER__PROFILE = 'SET__USER__PROFILE-profile-socialNetwork';
const SET__STATUS = 'SET__STATUS-profile-socialNetwork';
const DELETE__POST = 'DELETE__POST-profile-socialNetwork';
const SAVE__PHOTO__SUCCESS = 'SAVE__PHOTO__SUCCESS-profile-socialNetwork';

let initialState = {
    posts: [
        {post: 'How, are you?', likes: 20, id: 1},
        {post: 'Where are you?', likes: 20, id: 2},
    ],
    profile: null,
    status: ''
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD__POST: {
            let postBody = {
                post: action.newPost,
                likes: 20,
                id: 3
            }
            return {
                ...state,
                posts: [...state.posts, postBody],
            }
        }

        case CHANGE__VALUE: {
            return {
                ...state,
                newPost: action.newVal
            }
        }

        case SET__USER__PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }
        case SET__STATUS: {
            return {
                ...state,
                status: action.status
            }
        }

        case DELETE__POST: {
            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.postId)
            }
        }

        case SAVE__PHOTO__SUCCESS: {
            return {
                ...state,
                profile: {...state.profile, photos: action.photo}
            }
        }


        default: {
            return state;
        }
    }
}

export const addPost = (newPost) => ({type: ADD__POST, newPost});
export const changeVal = (newVal) => ({type: CHANGE__VALUE, newVal});
export const setUserProfile = (profile) => ({type: SET__USER__PROFILE, profile});
export const setStatus = (status) => ({type: SET__STATUS, status});
export const deletePost = (postId) => ({type: DELETE__POST, postId});
export const savePhotoSuccess = (photo) => ({type: SAVE__PHOTO__SUCCESS, photo});

export const getUserProfile = (userId) => {
    return async (dispatch) => {
        let data = await profileAPI.getProfile(userId)
        dispatch(setUserProfile(data));
    }
}

export const getUserStatus = (userId) => {
    return async (dispatch) => {
        let data = await profileAPI.getStatus(userId)
        dispatch(setStatus(data));
    }
}

export const updateUserStatus = (status) => {
    return async (dispatch) => {
        let data = await profileAPI.updateStatus(status)
        if (data.resultCode === 0) {
            dispatch(setStatus(status));
        }
    }
}

export const savePhoto = (photo) => {
    return async (dispatch) => {
        let data = await profileAPI.savePhoto(photo)
        if (data.resultCode === 0) {
            dispatch(savePhotoSuccess(data.data.photos));
        }
    }
}

export const sendData = (obj) => {
    return async (dispatch, getState) => {
        const userId = getState().auth.id;
        let data = await profileAPI.sendData(obj);
        if (data.resultCode === 0) {
            dispatch(getUserProfile(userId));
        } else {
            const message = data.messages.length > 0 ? data.messages[0] : 'some Error';
            dispatch(stopSubmit('infoForm', {_error: message}));
            return Promise.reject(message);
        }
    }
}

export default profileReducer;