import React from 'react';
import style from './Users.module.css';
import userImg from '../../assets/img/userAvatar.png';
import {NavLink} from 'react-router-dom'
import {followSuссess, toggleFollowingProcess, unFollowSuссess} from "../../redux/usersReducer";
import {useDispatch} from "react-redux";
import cn from "classnames";


const User = ({name, age, status, photos, id, followed, isFollowing}) => {
    const dispatch = useDispatch();

    return (
        <>
            <NavLink to={'/profile/' + id}>
                <div className={style.userPhoto}><img
                    src={photos.small !== null ? photos.small : userImg}
                    alt="Аватар"/></div>
            </NavLink>
            <div className={style.userName}>{name}</div>
            <div className={style.userAge}>{age}</div>
            <div className={style.userStatus}>{status}</div>
            <div className={style.userCountry}>{'user.location.country'}</div>
            <div className={style.userCity}>{'user.location.city'}</div>
            <button className={cn('button', style.button)}
                    disabled={isFollowing.some(userId => userId === id)}
                    onClick={() => {
                        dispatch(toggleFollowingProcess(true, id));
                        followed ?
                            dispatch(unFollowSuссess(id))
                            :
                            dispatch(followSuссess(id))
                    }

                    }>{followed ? 'follow' : 'unfollow'}
            </button>

        </>
    )
}

export default User;