import React, {useEffect, useState} from 'react';
import style from './Profile.module.css';
import {useDispatch, useSelector} from "react-redux";
import {updateUserStatus} from "../../redux/profileReducer";

const ProfileStatus = ({status}) => {
    const [editMode, setEditMode] = useState(false);
    const [newStatus, setNewStatus] = useState(status);
    const {userId} = useSelector(state => state.profile.profile);
    const {id} = useSelector(state => state.auth);

    useEffect(() => {
        setNewStatus(status)
    }, [status]);

    const activateEditMode = () => {
        setEditMode(true);
    }

    const deactivateEditMode = (e) => {
        setEditMode(false);
        if (userId === id) {
            dispatch(updateUserStatus(e.target.value));
        }
    }

    const dispatch = useDispatch();

    const onChangeStatus = (e) => {
        let status = e.target.value;
        setNewStatus(status);
    }

    return (
        <div>
            {editMode && <div className={style.input} onBlur={deactivateEditMode} onChange={onChangeStatus}>
                <input type="text" value={newStatus} autoFocus={true}/>
            </div>}
            {!editMode &&
            <span className={style.statusText} onDoubleClick={activateEditMode}>{status || '-----'}</span>}
        </div>
    )
}

export default ProfileStatus;