import {authAPI} from "../api/api";
import {authData} from "./authReducer";

const INITIALIZED__SUCCESS = 'INITIALIZED__SUCCESS-socialNetwork';


let initialState = {
    initialized: false
};

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case INITIALIZED__SUCCESS: {
            return {
                ...state,
                initialized: true
            }
        }

        default: {
            return state;
        }
    }
}


export const initializedSuccess = () => ({type: INITIALIZED__SUCCESS});
export const initializeApp = () => {
    return async (dispatch) => {
        let promise = await dispatch(authData());
        Promise.all([promise])
        dispatch(initializedSuccess());
    }
}


export default appReducer;