import React, {useEffect} from 'react';
import style from './Users.module.css';
import {useDispatch, useSelector} from "react-redux";
import {
    getUsers
}
    from "../../redux/usersReducer";
import cn from "classnames";
import Preloader from "../../common/Preloader/Preloader";
import Paginator from "../../common/Paginator/Paginator";
import User from "./User";


const Users = (props) => {
    const {users, portionSize, totalUsersCount, currentPage, isFetching, isFollowing} = useSelector(state => state.users);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getUsers(portionSize, currentPage));
    }, []);

    return (
        <>
            {isFetching ? <Preloader/>
                : <div className={cn('content', style.content)}>
                    <Paginator totalUsersCount={totalUsersCount}/>
                    {users.map((user, index) =>
                        <div key={index + '_' + user.name} className={style.userBlock}>
                            <User {...user} isFollowing={isFollowing}/>
                        </div>
                    )}
                </div>

            }

        </>
    )
}

export default Users;